import { ReactElement, createElement, useEffect, useState } from "react";

import RoundedCloseIcon from "../resources/RoundedCloseIcon";
import UserInfoLeftPanelImg from "../resources/UserInfoLeftPanelImg";
import { UserInfoProps } from "typings/UserInfoProps";
import { UserInfoType } from "../../typings/UserInfoType";
import bem from "../utils/bem";
import classNames from "classnames";
import { getProfileData } from "../utils/userInfo";

const loginClass = "login";
const rightPanelClass = "right";
const leftPanelClass = "left";
const rightTitle = bem([rightPanelClass, "title"]);
const rightSubtitle = bem([rightPanelClass, "subtitle"]);

export const UserInfo = ({
    idTokenProviderMf,
    decryptedTokenValue,
    userProviderMf,
    usernameAttribute,
    emailAttribute,
    onSave,
    onCloseUserInfo,
    usernameValid,
    emailValid,
    translation
}: UserInfoProps): ReactElement => {
    const [userData, setUserData] = useState<UserInfoType>({ name: "", email: "", userRoles: [] });
    const [editMode, setEditMode] = useState(false);

    useEffect(() => {
        getProfileData({
            idTokenProviderMf,
            decryptedTokenValue,
            userProviderMf,
            usernameAttribute,
            emailAttribute
        }).then((userData: UserInfoType) => {
            if (!userData || !userData.name || !userData.email) {
                setEditMode(true);
            }
            setUserData(userData);
        });
    }, [decryptedTokenValue, emailAttribute, idTokenProviderMf, userProviderMf, usernameAttribute]);

    return (
        <div className={bem([loginClass])}>
            <button className={bem([loginClass, "closeBtn"])} onClick={onCloseUserInfo}>
                <RoundedCloseIcon className={bem([loginClass, "closeBtn-icon"])} />
            </button>
            <div className={bem([leftPanelClass, "frame"])}>
                <UserInfoLeftPanelImg className={bem([leftPanelClass, "img"])} />
                <h3 className={bem([leftPanelClass, "title"])}>{translation.Feedback}</h3>
                <span className={bem([leftPanelClass, "subtitle"])}>{translation.Leave_feedback_in_context_}</span>
            </div>
            <div className={bem([rightPanelClass, "frame"])}>
                <div>
                    <span className={rightTitle}>{translation.Name}</span>
                    {!editMode && <span className={rightSubtitle}>{userData?.name}</span>}
                    {editMode && (
                        <input
                            className={classNames(
                                rightSubtitle,
                                bem(rightPanelClass, "edit"),
                                !usernameValid && bem(rightPanelClass, "invalid")
                            )}
                            type="text"
                            value={userData?.name}
                            onChange={e => {
                                setUserData({ ...userData, name: e.target.value });
                            }}
                        />
                    )}
                </div>
                <div>
                    <span className={rightTitle}>{translation.Email_Address}</span>
                    {!editMode && <span className={rightSubtitle}>{userData?.email}</span>}
                    {editMode && (
                        <input
                            className={classNames(
                                rightSubtitle,
                                bem(rightPanelClass, "edit"),
                                !emailValid && bem(rightPanelClass, "invalid")
                            )}
                            type="text"
                            value={userData?.email}
                            onChange={e => {
                                setUserData({ ...userData, email: e.target.value });
                            }}
                        />
                    )}
                </div>
                <button className={bem([loginClass, "btn"])} onClick={() => onSave(userData)}>
                    {translation.Continue}
                </button>
            </div>
        </div>
    );
};
