export const getBaseWebModelerUrl = (environmentName: string | null): string => {
    const env = environmentName ? `${environmentName}.` : "";
    return `https://studio.${env}mendix.com/`;
};

export enum STATUS {
    OK = 200,
    BAD_REQUEST = 400,
    UNAUTHORIZED = 401,
    NOT_FOUND = 404,
    SERVER_ERROR = 500
}

export const ATLAS_SCROLLABLE_AREA_SELECTOR =
    ".mx-scrollcontainer-fixed > .mx-scrollcontainer-middle > .mx-scrollcontainer-wrapper";

export const DEFAULT_FEEDBACK_SERVER_URL = "https://feedback-api.mendix.com/";
