import React, { CSSProperties, ReactElement, createElement, useState } from "react";

import { FeedbackForm } from "./FeedbackForm";
import { PopupOverlay } from "./PopupOverlay";
import { Result } from "./Result";
import { ScreenshotContainer } from "./ScreenshotContainer";
import { StartButton } from "./StartButton";
import { UserInfo } from "./UserInfo";
import { UserInfoType } from "../../typings/UserInfoType";
import bem from "../utils/bem";
import callFeedbackAPI from "../utils/submitFeedback";

interface RootProps {
    className: string;
    style?: CSSProperties;
    tabIndex?: number;
    sprintrapp: string;
    allowScreenshot: boolean;
    idTokenProviderMf: string;
    decryptedTokenValue: string;
    userProviderMf: string;
    userEntity: string;
    usernameAttribute: string;
    emailAttribute: string;
    feedbackServerLocation: string;
    foreignObjectRendering: boolean;
    scrollableAreaSelector: string;
    translation: Record<string, string | undefined>;
}

export function Root({
    style,
    className,
    tabIndex,
    idTokenProviderMf,
    userProviderMf,
    scrollableAreaSelector,
    foreignObjectRendering,
    sprintrapp,
    decryptedTokenValue,
    usernameAttribute,
    emailAttribute,
    userEntity,
    feedbackServerLocation,
    allowScreenshot,
    translation
}: RootProps): ReactElement {
    const [showUserInfo, setShowUserInfo] = useState<boolean>(false);
    const [showScreenshotForm, setShowScreenshotForm] = useState<boolean>(false);
    const [showFeedbackForm, setShowFeedbackForm] = useState<boolean>(false);
    const [showResult, setShowResult] = useState<boolean>(false);
    const [userInfo, setUserInfo] = useState<UserInfoType>();
    const [screenshot, setScreenshot] = useState<string>("");
    const [error, setError] = useState<string>("");
    const [submitInProgress, setSubmitInProgress] = useState<boolean>(false);
    const [userInfoValid, setUserInfoValid] = useState<boolean>(false);
    const [usernameValid, setUsernameValid] = useState<boolean>(true);
    const [emailValid, setEmailValid] = useState<boolean>(true);
    const [feedbackCommentValid, setFeedbackCommentValid] = useState<boolean>(true);

    const submitUserInfo = ({ name, email, userRoles }: UserInfoType): void => {
        if (validateUserInfo(name, email)) {
            setUserInfo({ name, email, userRoles });
            setShowUserInfo(false);

            if (allowScreenshot) {
                setShowScreenshotForm(true);
            } else {
                setShowFeedbackForm(true);
            }
        }
    };

    const submitFeedback = ({ comment, includeScreenshot }: { comment: string; includeScreenshot: boolean }): void => {
        if (validateFeedbackComment(comment)) {
            setSubmitInProgress(true);
            callFeedbackAPI({
                sprintrId: sprintrapp,
                feedbackServer: feedbackServerLocation,
                screenshot: includeScreenshot ? screenshot : null,
                username: userInfo ? userInfo.name : "",
                email: userInfo ? userInfo.email : "",
                userRoles: userInfo ? userInfo.userRoles : [],
                title: comment,
                description: ""
            })
                .catch(error => {
                    console.warn(`Feedback Widget >> Failed submitting feedback with the following error: ${error}`);
                    setSubmitInProgress(false);
                    setError(error);
                    setShowFeedbackForm(false);
                    setShowResult(true);
                })
                .then(() => {
                    setSubmitInProgress(false);
                    setError("");
                    setShowFeedbackForm(false);
                    setShowResult(true);
                });
        }
    };

    const validateUserInfo = (name: string, email: string): boolean => {
        if (!name || !email) {
            setUserInfoValid(false);

            if (!name) {
                setUsernameValid(false);
            } else {
                setUsernameValid(true);
            }

            if (!email) {
                setEmailValid(false);
            } else {
                setEmailValid(true);
            }
            return false;
        }

        setUsernameValid(true);
        setEmailValid(true);
        setUserInfoValid(true);

        return true;
    };

    const validateFeedbackComment = (comment: string): boolean => {
        if (!comment) {
            setFeedbackCommentValid(false);
            return false;
        }

        setFeedbackCommentValid(true);
        return true;
    };

    const onCloseResult = (): void => {
        setShowResult(false);
    };

    const onCloseUserInfo = (): void => {
        setShowUserInfo(false);
    };

    const onClosePopupOverlay = (): void => {
        setShowUserInfo(false);
        setShowFeedbackForm(false);
        setShowResult(false);
        setShowScreenshotForm(false);
    };

    return (
        <React.Fragment>
            <StartButton
                onClick={() => setShowUserInfo(true)}
                style={style}
                className={`${bem(["startBtn"])} ${className}`}
                tabIndex={tabIndex}
            ></StartButton>
            {(showUserInfo || showFeedbackForm) && <PopupOverlay onClosePopupOverlay={onClosePopupOverlay} />}
            {showUserInfo && (
                <UserInfo
                    onSave={submitUserInfo}
                    onCloseUserInfo={onCloseUserInfo}
                    idTokenProviderMf={idTokenProviderMf}
                    userProviderMf={userProviderMf}
                    userEntity={userEntity}
                    usernameAttribute={usernameAttribute}
                    emailAttribute={emailAttribute}
                    decryptedTokenValue={decryptedTokenValue}
                    usernameValid={usernameValid}
                    emailValid={emailValid}
                    translation={translation}
                ></UserInfo>
            )}
            {showScreenshotForm && userInfoValid && (
                <ScreenshotContainer
                    scrollableAreaSelector={scrollableAreaSelector}
                    foreignObjectRendering={foreignObjectRendering}
                    onSave={(capture: string) => {
                        setScreenshot(capture);
                        setShowScreenshotForm(false);
                        setShowFeedbackForm(true);
                    }}
                    onExit={() => {
                        setShowUserInfo(true);
                        setShowScreenshotForm(false);
                    }}
                    translation={translation}
                ></ScreenshotContainer>
            )}
            {showFeedbackForm && userInfoValid && (
                <FeedbackForm
                    submitInProgress={submitInProgress}
                    onSubmit={submitFeedback}
                    onCancel={() => {
                        setShowScreenshotForm(true);
                        setShowFeedbackForm(false);
                        setSubmitInProgress(false);
                    }}
                    feedbackCommentValid={feedbackCommentValid}
                    allowScreenshot={allowScreenshot}
                    translation={translation}
                ></FeedbackForm>
            )}
            <Result
                showResult={showResult}
                error={error}
                onCloseResult={onCloseResult}
                translation={translation}
            ></Result>
        </React.Fragment>
    );
}
