import { ReactElement, createElement, useEffect, useRef, useState } from "react";

import { ScreenshotContainerProps } from "typings/ScreenshotContainerProps";
import bem from "../utils/bem";
import captureScreen from "../utils/captureScreen";
import getCursorPosition from "../utils/getCursorPosition";

const componentName = "screenshotFrame";

const FrameSide = ({ side }: { side: string }): ReactElement => (
    <div data-html2canvas-ignore="true" className={bem([componentName, `${side}`])} />
);

interface PositionType {
    dotX: number;
    dotY: number;
}

export const ScreenshotContainer = ({
    scrollableAreaSelector,
    foreignObjectRendering,
    onSave,
    onExit,
    translation
}: ScreenshotContainerProps): ReactElement => {
    const [pointerPosition, setPointerPosition] = useState<PositionType>();
    const pointerSize = 30;
    const menuRef = useRef<HTMLDivElement>(null);

    const capture = async (): Promise<void> => {
        const screenshot = await captureScreen(scrollableAreaSelector, foreignObjectRendering).catch(error => {
            console.error(error); // eslint-disable-line no-console
            return null;
        });
        onSave(screenshot);
    };

    const onPointerClick = (event: MouseEvent): void => {
        if (menuRef.current && menuRef.current.contains(event.target as Node)) {
            return;
        }

        const { dotX, dotY } = getCursorPosition(event);
        setPointerPosition({
            dotX: dotX - pointerSize / 2,
            dotY: dotY - pointerSize / 2
        });

        capture();
    };

    useEffect(() => {
        document.body.addEventListener("mousedown", onPointerClick);
        return () => document.body.removeEventListener("mousedown", onPointerClick);
    }, []);

    return (
        <div className={bem([componentName])}>
            <div className={bem([componentName, "top"])} data-html2canvas-ignore="true" ref={menuRef}>
                {translation.You_are_now_in_feedback_mode}&nbsp;
                {translation.Click_anywhere_on_the_screen_to_leave_a_comment}
                <button className={bem([componentName, "exitBtn"])} onClick={onExit}>
                    {translation.Exit_Feedback_Mode}
                </button>
            </div>
            <FrameSide side="right" />
            <FrameSide side="bottom" />
            <FrameSide side="left" />

            {pointerPosition && (
                <div
                    className={bem([componentName, "dotPointer"])}
                    style={{
                        top: pointerPosition.dotY,
                        left: pointerPosition.dotX
                    }}
                />
            )}
        </div>
    );
};
