import { UserInfoType } from "typings/UserInfoType";
import { callMicroflow } from "./mxHelpers";
import { loadLocalUserData } from "./customUserEntity";
import { parseIdToken } from "./idToken";

/**
 * Source Order:
 * 1. MendixSSO (idToken)
 * 2. Local login (login directly via the App)
 */
export const getProfileData = async ({
    idTokenProviderMf,
    decryptedTokenValue,
    userProviderMf,
    usernameAttribute,
    emailAttribute
}: {
    idTokenProviderMf: string;
    userProviderMf: string;
    usernameAttribute: string;
    emailAttribute: string;
    decryptedTokenValue?: string;
}): Promise<UserInfoType> => {
    /**
     * if the app allows anonymous users, idToken will be null, even if you set idTokenProviderMf correctly
     * So we need to fallback to local login
     */
    if (idTokenProviderMf) {
        const idToken = await callMicroflow<string>(idTokenProviderMf);
        if (idToken) {
            return getProfileViaIdToken({ idToken, usernameAttribute, emailAttribute, decryptedTokenValue });
        }
    }

    // 2. custom authentication

    if (userProviderMf) {
        const userObject = await callMicroflow<{ [attribute: string]: string }>(userProviderMf);
        const usernameFromUserObj = userObject && userObject[usernameAttribute];
        const emailFromUserObj = userObject && userObject[emailAttribute];
        const { name: localUsername, email: localEmail, userRoles } = await loadLocalUserData(
            usernameAttribute,
            emailAttribute
        );

        const username = usernameFromUserObj || localUsername;
        const email = emailFromUserObj || localEmail;

        return {
            name: username,
            email,
            userRoles
        };
    }

    // 3. local authentication
    const userData = await loadLocalUserData(usernameAttribute, emailAttribute);
    return userData;
};

const getProfileViaIdToken = async ({
    idToken,
    usernameAttribute,
    emailAttribute,
    decryptedTokenValue
}: {
    idToken: string | Record<string, string>;
    usernameAttribute: string;
    emailAttribute: string;
    decryptedTokenValue?: string;
}): Promise<UserInfoType> => {
    const tokenValuePropertyName = decryptedTokenValue ? decryptedTokenValue : "Value";
    const tokenValue = typeof idToken === "string" ? idToken : idToken[tokenValuePropertyName];
    const { displayName, email: idTokenEmail } = parseIdToken(tokenValue);

    // map the response to username and email in the context of this widget
    const { name: localUsername, email: localEmail, userRoles } = await loadLocalUserData(
        usernameAttribute,
        emailAttribute
    );

    const username = displayName || localUsername;
    const email = idTokenEmail || localEmail;

    return {
        name: username,
        email,
        userRoles
    };
};
