import jwtDecode from "jwt-decode";

export const parseIdToken = (
    idToken: string
): {
    displayName: string;
    email: string;
} => {
    if (!idToken) {
        return {
            displayName: "",
            email: ""
        };
    }
    const decoded = jwtDecode<{ [key: string]: Record<string, string> }>(idToken);
    const { display_name: displayName, email } = decoded["mx:user:profile:v1"];
    delete decoded["mx:user:profile:v1"];
    return {
        displayName,
        email
    };
};
