const replaceSpaceWithSymbol = (text: string, symbol = "_"): string => {
    return text.split(" ").join(symbol);
};

const parseModifierObject = (obj: { [key: string]: string }): string => {
    return Object.entries(obj)
        .filter(([, value]) => !!value)
        .map(([key]) => `${replaceSpaceWithSymbol(key)}`)
        .join("--");
};

const concatBEM = (elements: string, modifier: string, delimeter = "--", underscore = "__"): string => {
    let block = "mx-feedback";
    block = block + underscore + elements;

    return [block, modifier].filter(x => x).join(delimeter);
};

const bem = (elements: string | string[], modifier: string | { [key: string]: string } = ""): string => {
    const processString = (text: string): string => replaceSpaceWithSymbol(text);

    const processedModifier = typeof modifier === "object" ? parseModifierObject(modifier) : processString(modifier);
    const processedElements =
        typeof elements === "string"
            ? replaceSpaceWithSymbol(elements)
            : elements.map(e => replaceSpaceWithSymbol(e)).join("-");

    return concatBEM(processedElements, processedModifier);
};

export default bem;
