import { ReactElement, createElement, useState } from "react";

import { DEFAULT_FEEDBACK_SERVER_URL } from "../config";
import { FeedbackFormProps } from "typings/FeedbackFormProps";
import RoundedCloseIcon from "../resources/RoundedCloseIcon";
import bem from "../utils/bem";
import classNames from "classnames";

const componentName = "comment";
const btnBaseClass = "comment-btn";
const contentClass = "content";

export const FeedbackForm = ({
    submitInProgress,
    onSubmit,
    onCancel,
    feedbackCommentValid,
    allowScreenshot,
    translation
}: FeedbackFormProps): ReactElement => {
    const [comment, setComment] = useState("");
    const [includeScreenshot, setIncludeScreenshot] = useState(!!allowScreenshot);

    const toggleIncludeShot = (): void => {
        setIncludeScreenshot(prevState => {
            return !prevState;
        });
    };

    const handleComment = (e: { target: { value: string } }): void => {
        setComment(e.target.value);
    };

    return (
        <div className={bem([componentName])}>
            <button className={bem([componentName, "closeBtn"])} onClick={onCancel}>
                <RoundedCloseIcon className={bem([componentName, "closeBtn-icon"])} />
            </button>
            <h3 className={bem([componentName, "title"])}>{translation.Add_a_comment}</h3>
            <form id="comment" action={DEFAULT_FEEDBACK_SERVER_URL} method="post">
                <div
                    className={classNames(
                        bem([componentName, "content"]),
                        !feedbackCommentValid && bem([componentName, contentClass], "invalid")
                    )}
                >
                    <textarea
                        className={bem([componentName, "textArea"])}
                        name="comment_content"
                        rows={4}
                        cols={50}
                        value={comment}
                        onChange={handleComment}
                    />
                </div>
                {allowScreenshot && (
                    <label className={bem([componentName, "checkbox"])}>
                        <input
                            type="checkbox"
                            id="includeScreenshot"
                            name="includeScreenshot_checkbox"
                            checked={includeScreenshot}
                            className={bem([componentName, "check"])}
                            onChange={toggleIncludeShot}
                        ></input>
                        <span className={bem([componentName, "tag"])}>
                            {translation.Include_a_screenshot_of_the_current_page}
                        </span>
                    </label>
                )}
            </form>
            <div
                className={classNames(
                    bem([btnBaseClass, "group"]),
                    !allowScreenshot && bem([btnBaseClass, "group"], "screenshowNotAllowed")
                )}
            >
                <button className={bem(btnBaseClass, "cancel")} form="comment" type="button" onClick={onCancel}>
                    {translation.Cancel}
                </button>
                <button
                    className={bem(btnBaseClass, "submit")}
                    form="comment"
                    type="button"
                    disabled={submitInProgress}
                    onClick={() => {
                        if (submitInProgress) {
                            return;
                        }
                        onSubmit({ comment, includeScreenshot });
                    }}
                >
                    {submitInProgress ? `${translation.Sending_feedback}` : `${translation.Submit}`}
                </button>
            </div>
        </div>
    );
};
