import { getUserObject, session } from "./mxHelpers";

import { MxObject } from "typings/mx";
import { UserInfoType } from "typings/UserInfoType";

const getUsername = (userObj: MxObject, usernameAttribute: string): string => {
    const userGuid = userObj.getGuid();
    const hasSession = userGuid && Number(userGuid) !== 0;
    const hasUsernameAttribute = usernameAttribute && userObj.has(usernameAttribute);
    const getUsernameFromSession = (): string => {
        const isNonGuest = !session.isGuest();
        return hasSession && isNonGuest ? session.getUsername() : "";
    };

    return hasUsernameAttribute ? (userObj.get(usernameAttribute) as string) : getUsernameFromSession();
};

const validateEmailAddress = (address: string): string => (address && address.match(/.+@.+\..+/) ? address : "");

const loadDataFromUserObject = (
    userObject: MxObject,
    usernameAttribute: string,
    emailAttribute: string
): UserInfoType => {
    const name = getUsername(userObject, usernameAttribute);
    const userObjectHasEmailAddress = userObject && emailAttribute && userObject.has(emailAttribute);
    const email = userObjectHasEmailAddress ? (userObject.get(emailAttribute) as string) : validateEmailAddress(name); // if username is a valid email, use it as its email
    const userRoles = session.getUserRoleNames();
    return { name, email, userRoles };
};

// usernameAttribute and emailAttribute are retrieved from modeler
// they are passed in via the props of the root component, i.e. index.js
export const loadLocalUserData = async (usernameAttribute: string, emailAttribute: string): Promise<UserInfoType> => {
    const userObject = await getUserObject();

    return userObject
        ? loadDataFromUserObject(userObject, usernameAttribute, emailAttribute)
        : { name: "", email: "", userRoles: [] };
};
