import { getScroll } from "./getCursorPosition";
import html2canvas from "html2canvas";

const captureScreen = async (selector: string, foreignObjectRendering: boolean): Promise<string> => {
    /**
     *  1. user-provided selector
     *  2. default Atlas scrollable area selector
     *  3. if document.querySelector returns nothing, use document.body instead
     */
    const { x, y } = getScroll(selector);

    const canvas = await html2canvas(document.documentElement, {
        /**
         * https://github.com/niklasvh/html2canvas/issues/1295
         * It's a known issue.
         * If you set foreignObjectRendering to true, onClone will not be triggered.
         * Then this introduced the scrolling issue
         * But, setting foreignObjectRendering to false will result in a critical issue:
         * the shadow of an pop-op will block the page, so the screenshot is useless.
         *
         * 2020-09-28
         * We decided to bring back the scrolling issue because the pop-up issue is more serious.
         */
        foreignObjectRendering,
        onclone: clone => {
            const element = clone.querySelector(selector) || clone.body;

            element.scrollTop = y;
            element.scrollLeft = x;
        }
    }).catch(() => {
        // eslint-disable-next-line no-console
        console.warn(`
            Feedback Widget >> An error occurred while capturing screenshot.
            It is an known issue of a library (html2canvas) that is used in the Feedback widget.
            As a fallback solution, we take a full-page screenshot, 
            but the orange dot position will not be correct if user "scrolled" the page.

            We will publish new version as soon as the library has new release.
        `);
        // as a fallback: take a screenshot that scrolling is ignored.
        return html2canvas(document.body, {
            foreignObjectRendering,
            windowHeight: document.querySelector("div.mx-page")?.clientHeight,
            scrollY: 0
        });
    });

    const dataUrl = canvas.toDataURL();
    if (canvas && canvas.remove) {
        canvas.remove();
    }

    return dataUrl;
};

export default captureScreen;
