import { ReactElement, createElement, useEffect } from "react";

import { ResultProps } from "typings/ResultProps";
import RoundedCloseIcon from "../resources/RoundedCloseIcon";
import bem from "../utils/bem";
import classNames from "classnames";

const componentName = "result";

export const Result = ({ showResult, error, onCloseResult, translation }: ResultProps): ReactElement => {
    if (error) {
        console.warn(`Feedback Widget error >> ${error}`);
    }

    const resultClass = showResult
        ? classNames(bem([componentName]), bem(componentName, "open"))
        : bem([componentName]);

    useEffect(() => {
        setTimeout(onCloseResult, 5000);
    }, [onCloseResult]);

    return (
        <div className={resultClass}>
            <button className={bem([componentName, "closeBtn"])} onClick={onCloseResult}>
                <RoundedCloseIcon className={bem([componentName, "closeBtn-icon"])} />
            </button>
            <span className={bem([componentName, "title"])}>
                {error ? `${translation.Error}` : `${translation.Feedback_submitted}`}
            </span>
            <span className={bem([componentName, "message"])}>
                {error
                    ? `${translation.Error_sending_your_feedback_Please_try_again_later}`
                    : `${translation.Thanks_for_your_feedback_We_will_come_back_to_you_soon}`}
            </span>
        </div>
    );
};
