import "./sass/feedback.scss";

import { ReactElement, createElement } from "react";

import { DEFAULT_FEEDBACK_SERVER_URL } from "./config";
import { Root } from "./components/Root";
import { SprintrFeedbackContainerProps } from "typings/SprintrFeedbackProps";

export default function SprintrFeedback({
    class: className,
    style,
    tabIndex,
    idTokenProviderMf,
    userProviderMf,
    scrollableAreaSelector,
    foreignObjectRendering,
    sprintrapp,
    decryptedTokenValue,
    usernameAttribute,
    emailAttribute,
    userEntity,
    overrideFeedbackServerLocation,
    allowScreenshot,
    translation_Cancel,
    translation_Submit,
    translation_Continue,
    translation_Feedback,
    translation_Name,
    translation_Email_Address,
    translation_You_are_now_in_feedback_mode,
    translation_Click_anywhere_on_the_screen_to_leave_a_comment,
    translation_Exit_Feedback_Mode,
    translation_Add_a_comment,
    translation_Include_a_screenshot_of_the_current_page,
    translation_Screenshots_have_been_disabled_for_the_feedback_mode,
    translation_Leave_feedback_in_context_,
    translation_Take_screenshot,
    translation_Error,
    translation_Sending_feedback,
    translation_Feedback_submitted,
    translation_Error_creating_a_screenshot,
    translation_Thanks_for_your_feedback_We_will_come_back_to_you_soon,
    translation_Error_sending_your_feedback_Please_try_again_later
}: SprintrFeedbackContainerProps): ReactElement {
    const translation = {
        Cancel: translation_Cancel?.value,
        Submit: translation_Submit?.value,
        Continue: translation_Continue?.value,
        Feedback: translation_Feedback?.value,
        Name: translation_Name?.value,
        Email_Address: translation_Email_Address?.value,
        You_are_now_in_feedback_mode: translation_You_are_now_in_feedback_mode?.value,
        Click_anywhere_on_the_screen_to_leave_a_comment:
            translation_Click_anywhere_on_the_screen_to_leave_a_comment?.value,
        Exit_Feedback_Mode: translation_Exit_Feedback_Mode?.value,
        Add_a_comment: translation_Add_a_comment?.value,
        Include_a_screenshot_of_the_current_page: translation_Include_a_screenshot_of_the_current_page?.value,
        Screenshots_have_been_disabled_for_the_feedback_mode:
            translation_Screenshots_have_been_disabled_for_the_feedback_mode?.value,
        Leave_feedback_in_context_: translation_Leave_feedback_in_context_?.value,
        Take_screenshot: translation_Take_screenshot?.value,
        Error: translation_Error?.value,
        Sending_feedback: translation_Sending_feedback?.value,
        Feedback_submitted: translation_Feedback_submitted?.value,
        Error_creating_a_screenshot: translation_Error_creating_a_screenshot?.value,
        Thanks_for_your_feedback_We_will_come_back_to_you_soon:
            translation_Thanks_for_your_feedback_We_will_come_back_to_you_soon?.value,
        Error_sending_your_feedback_Please_try_again_later:
            translation_Error_sending_your_feedback_Please_try_again_later?.value
    };

    const feedbackServerLocation = overrideFeedbackServerLocation
        ? overrideFeedbackServerLocation
        : DEFAULT_FEEDBACK_SERVER_URL;

    return (
        <Root
            style={style}
            className={className}
            tabIndex={tabIndex}
            idTokenProviderMf={idTokenProviderMf}
            userProviderMf={userProviderMf}
            scrollableAreaSelector={scrollableAreaSelector}
            foreignObjectRendering={foreignObjectRendering}
            sprintrapp={sprintrapp}
            decryptedTokenValue={decryptedTokenValue}
            usernameAttribute={usernameAttribute}
            emailAttribute={emailAttribute}
            userEntity={userEntity}
            feedbackServerLocation={feedbackServerLocation}
            allowScreenshot={allowScreenshot}
            translation={translation}
        />
    );
}
